const jwt = require('jsonwebtoken');
const {dbConnection,simpleSelectQuery}  = require('../database.js');

exports.getUser = async (req,res) =>{
	const rb = req.body;
	
	if(!req.headers.authorization ||
       !req.headers.authorization.startsWith('Bearer') ||
       !req.headers.authorization.split(' ')[1]){
		console.log('header not set')
	}else{

		const theToken = req.headers.authorization.split(' ')[1];
        jwt.verify(theToken, 'the-super-strong-secrect',(error,result)=>{
        	if(result != undefined)
        	{
        		let userId = result.id;
        		simpleSelectQuery(dbConnection,' users ',' * ',
					' WHERE id = '+userId)
				.then(row=>{
					if(row.length > 0){
						res.send('Utilisateur: '+ userId+ 
							" Name:"+ row[0].pseudo+ 
							' mail: '+row[0].mail)
					}else{console.log('Mail ou mot de passe incorrect')}
				})
				.catch(error =>console.log(error))
        	}else{
        		console.log('Token not found')
        	}
        });


		
	}
	

}
