const {dbConnection,simpleSelectQuery}  = require('../database.js');
exports.liste_etablissement = (req,res)=>{
	// console.log(.type)
	let rp = req.params;
	let typeEtablissementTable = '';
	let queryOtherParam = ''

	if(['Université','Ecole'].includes(rp.type)){
		typeEtablissementTable = 'universites_ecoles';
	}
	else if(rp.type == 'Collège') {
		typeEtablissementTable = 'colleges';
	}
	else if(rp.type == 'Lycée') {
		typeEtablissementTable = 'lycees';
	}
	queryOtherParam = ' WHERE nom LIKE "%'+rp.type+'%" ';
	simpleSelectQuery(dbConnection,typeEtablissementTable,' id,nom ',queryOtherParam+' ORDER BY nom ')
	.then(result => {
		res.send({text: 'ok',data: result})
	})

}