const bcrypt = require('bcryptjs');
const jwt = require('jsonwebtoken');
const {dbConnection,simpleSelectQuery}  = require('../database.js');

exports.login = async (req,res) =>{

	const rb = req.body;
	const hashPass = await bcrypt.hash(rb.password,12);
	
	simpleSelectQuery(dbConnection,' users ',' * ',
		' WHERE pseudo = "'+rb.pseudo+'" OR mail = "'+rb.mail+'" ')
	.then(row=>{
		if(row.length > 0){
			
			bcrypt.compare(rb.password, row[0].password, function(err, result) {
			   if(result){
			   	const theToken = jwt.sign({id:row[0].id},'the-super-strong-secrect',{ expiresIn: '1h' });
			   	res.json({token: theToken});
			   }else{
			   	console.log('Mot de passe incorrect')
			   }
			});

		}else{console.log('Mail ou mot de passe incorrect')}
	})
	.catch(error =>console.log(error))

}

