const {dbConnection,simpleSelectQuery}  = require('../database.js');
exports.liste_filière = (req,res)=>{
	// console.log(.type)
	
	// type_etablissement
	let rp = req.params;
	let typeFilière_SerieTable = '';


	if(['Université','Ecole'].includes(rp.type_etablissement)){
		typeFilière_SerieTable = 'filieres_universite'
	}else{
		typeFilière_SerieTable = 'filieres_series_lycee'
	}
	simpleSelectQuery(dbConnection,typeFilière_SerieTable,' * ',
		' ORDER BY nom ')
	.then(result => {
		res.send({text: 'ok',data: result})
	})

}

