const bcrypt = require('bcryptjs');
const {dbConnection,simpleSelectQuery,insertQuery}  = require('../database.js');

exports.register = async (req,res) =>{
	res.send('Tu te register')
	// console.log('regis'+ req.body.pseudo);

	const rb = req.body;
	const hashPass = await bcrypt.hash(rb.password, 12);

	
	simpleSelectQuery(dbConnection,' users ',' * ',
		' WHERE pseudo = "'+rb.pseudo+'" OR mail = "'+rb.mail+'" ')
	.then(row=>{
		if(row.length <= 0){
			insertQuery(dbConnection,
				' users(pseudo,mail,sexe,age,type_etablissement,nom_etablissement,filière_serie,password) ',
				'"'+rb.pseudo+'","'+rb.mail+'","'+rb.sexe+'",'+rb.age+','+
				'"'+rb.type_etablissement+'","'+rb.nom_etablissement+'","'+rb.filière_serie+'","'+hashPass+'"')
			.then(row=>{
					if(row.affectedRows == 1){
						console.log('Tu as été bien enregistré avec l\'id '+row.insertId)
					}
				})
			.catch(error =>console.log(error))

		}else{console.log('tu existe')}
	})
	.catch(error =>console.log(error))

}

