const simpleSelectQuery = (con,tableSelected,columnSelected,params)=>{
	return new Promise((resolve,reject)=>
		con.query('SELECT '+columnSelected+' FROM '+tableSelected+' '+params,(error,res)=>{
		if(error){
			reject(error)
		}else{
			resolve(res)
		}
	}))
}

const insertQuery = (con,tableSelected,values)=>{
	return new Promise((resolve,reject)=>
		con.query('INSERT INTO '+tableSelected+' VALUES('+values+') ',(error,res)=>{
		if(error){
			reject(error)
			// console.log(error)
		}else{
			// console.log(res)
			resolve(res)
		}
	}))
}
// export default dbConnection;
module.exports = {dbConnection,simpleSelectQuery,insertQuery};