import express from 'express';
import {register} from './Controllers/register.js'
import {login} from './Controllers/login.js'
import {getUser} from './Controllers/getUser.js'
import {liste_etablissement} from './Controllers/etablissements.js'
import {liste_filière} from './Controllers/filières.js'




const router = express.Router();

router.post('/register',register)
router.post('/login',login)
router.get('/Liste-Etablissement/:type',liste_etablissement)
router.get('/Liste-Filiere-Serie/:type_etablissement',liste_filière)



export default router;